* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  word-wrap: break-word;
}

::-webkit-scrollbar {
  width: 0.5rem;
  height: 0.5rem;
}
::-webkit-scrollbar-thumb {
  background: linear-gradient(#20ff0022, #20ff00);
  border-radius: 6px;
}
::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(transparent, #00c6ff);
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: "Poppins", sans-serif;
  min-height: 100vh;
  background: linear-gradient(to left bottom, #65dfc9, #6cdbeb);
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-container {
  background: white;
  min-height: 80vh;
  max-height: 80vh;
  width: 80%;
  background: linear-gradient(
    to left top,
    rgba(255, 255, 255, 0.8),
    rgba(255, 255, 255, 0.5)
  );
  border-radius: 2rem;
  z-index: 2;
  backdrop-filter: blur(10rem);
  position: relative;
  display: flex;
}

.main-container::before, .main-container::after {
  content: '';
  background: white;
  background: linear-gradient(
    to left top,
    rgba(255, 255, 255, 0.8),
    rgba(255, 255, 255, 0.3)
  );
  height: 10rem;
  width: 10rem;
  position: absolute;
  border-radius: 50%;
  z-index: 1;
}

.main-container::before {
  top: -2%;
  left: -10%;
}

.main-container::after {
  bottom: -2%;
  right: -10%;
}

.todos-container {
  margin: 1rem;
  padding: 0.5rem;
  flex: 3;
  max-width: 70%;
}

.todos-header {
  max-height: 100%;
  padding: 0.2rem;
  margin: 0.2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.side-container {
  padding: 1rem;
  flex: 1;
  max-width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(
    to right bottom,
    rgba(255, 255, 255, 0.7),
    rgba(255, 255, 255, 0.3)
  );
  border-radius: 0 2rem 2rem 0;
}

.current-todos {
  width: 100%;
  min-height: 80%;
  max-height: 80%;
  padding: 1rem;
  margin: 0.3rem;
  overflow-y: scroll;
  display: grid;
  grid-gap: 5rem 5rem;
  grid-template-columns: repeat(auto-fill, minmax(min-content, 10rem));
  grid-auto-rows: minmax(10rem, 10rem);
  border-bottom: 1px solid #000;
}

.quick-controls-container {
  margin: 0.5rem;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.controls-container {
  width: 100%;
  min-height: 50%;
  margin: 0.3rem;
  border-bottom: 1px solid #000;
}

.controls-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  overflow-y: scroll;
}

.cleared-todos-container {
  width: 100%;
  min-height: 50%;
}

.cleared-todos-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0.3rem;
}

.cleared-todos-wrapper {
  height: 90%;
  overflow-y: scroll;
}

.cleared-todos-wrapper {
  display: grid;
  grid-gap: 10%;
  grid-template-columns: repeat(auto-fill, minmax(min-content, 10rem));
  grid-auto-rows: minmax(10rem, 10rem);
}


.todo {
  position: relative;
  height: 100%;
  width: 100%;
  border-radius: 1rem;
  background-color: rgba(55, 151, 241, 0.356);
  box-shadow: 6px 6px 20px rgba(122, 122, 122, 0.212);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.todo-content {
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.todo-content * {
  border: none;
  outline: none;
  background: transparent;
}

.todo-content > div {
  margin: 0.2rem;
  margin: 0.2rem;
}

.current-todos .todo-content input:first-child {
  font-size: 1.2rem;
  font-weight: 1000;
}

.current-todos .todo-content textarea {
  width: 100%;
  font-size: 0.8rem;
}

.current-todos .todo-content input[type=date] {
  font-weight: 1000;
  font-size: 0.9rem;
}

.add-todo-form {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.todo.input .todo-content {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.todo.input .todo-content > div {
  border-bottom: 1px solid #000;
}

.edit-todo-control {
  bottom: 0;
  border-radius: 0 0 1rem 1rem;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.edit-todo-control > div {
  display: flex;
  justify-content: center;
  padding: 0.2rem;
  width: 50%;
}

.toggle-checked-btn  {
  border-radius: 0 0 0 1rem;
  background-color: rgb(6, 159, 230);
  cursor: pointer;
}

.toggle-edit-btn  {
  border-radius: 0 0 1rem 0;
  background-color: rgba(31, 164, 226, 0.836);
  cursor: pointer;
}

.toggle-checked-btn.active  {
  background-color: rgba(3, 65, 94, 0.692);
}

.toggle-edit-btn.active  {
  background-color: rgba(5, 102, 131, 0.699);
}

.toggle-checked-btn:hover, .toggle-edit-btn:hover {
  background-color: rgb(41, 184, 250);
}


.btn-type-1 {
  display: inline-block;
  outline: none;
  border: 1px solid  #000000;
  background-color: #BDF1F6;
  color: black;
  padding: 0.2rem;
  border-radius: 0.105rem;
  cursor: pointer;
  transition: 0.05s ease;
  box-shadow: 0px 0px 5px rgba(122, 122, 122, 0.8);
}

.btn-type-1:hover {
  background: transparent;
}

textarea {
  resize: none;
}

label {
  font-size: 0.7rem;
}